  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  overflow-x: hidden;
  background-color: #ECE7E3;
}

p {
  color: #373737;
  font-size: 1.7rem;
  font-family: "Loubag", sans-serif;
}

.container {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

.content__center {
  width: 90%;
  max-width: 70rem;
  margin: 0 auto;
  margin-bottom: 4rem;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img,
picture {
  width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4 {
  font-family: "Sugar", sans-serif;
  margin: 2.5rem 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

@font-face {
  font-family: "Loubag";
  src: url(../fonts/TTF/Loubag-Thin.ttf);
}
@font-face {
  font-family: "Mango";
  src: url(../fonts/TTF/BlackMango-Thin.ttf);
}
@font-face {
  font-family: "Sugar";
  src: url(../fonts/TTF/Brown-Sugar.ttf);
}
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/* UI */
.nav {
  width: 100%;
  height: 6rem;
  background-color: #ECE7E3;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  transition: all 300ms ease;
}

.nav__content {
  max-width: 120rem;
  height: 100%;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__content .nav__btn {
  padding: 0.4rem;
  display: flex;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 5rem;
}
@media (min-width: 768px) {
  .nav__content .nav__btn {
    display: none;
  }
}
.nav__content .nav__background {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.3);
  display: none;
}
.nav__content .nav__elements {
  width: 17rem;
  height: 100vh;
  padding: 2rem 2rem;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
  transform: translateX(170px);
  background-color: #ECE7E3;
  transition: all 300ms ease;
}
@media (min-width: 768px) {
  .nav__content .nav__elements {
    transform: translateX(0);
    width: auto;
    height: 100%;
    padding: 0;
    position: static;
    background-color: rgba(0, 0, 0, 0);
  }
}
.nav__content .nav__elements .nav__link {
  text-transform: uppercase;
  font-family: "Loubag", sans-serif;
}
.nav__content .nav__elements .nav__link a {
  font-size: 1.5rem;
}
.nav__content a {
  color: #373737;
}

.nav__logo h2 {
  font-size: 3rem;
  font-family: "Loubag", sans-serif;
}

.nav__elements .nav__list {
  width: 100%;
  height: auto;
  margin-top: 5rem;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 768px) {
  .nav__elements .nav__list {
    width: 100%;
    height: 100%;
    margin-top: 0;
    flex-direction: row;
    align-items: center;
  }
}
.nav__elements .nav__list a {
  font-size: 2rem;
}
.nav__elements .nav__name {
  text-align: start;
  font-family: "Loubag", sans-serif;
}
@media (min-width: 768px) {
  .nav__elements .nav__name {
    display: none;
  }
}

.scrolled-down {
  height: 7rem;
  box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.17);
}

/* PORTRATE */
.homepage {
  overflow: hidden;
}

.portrate {
  width: 600%;
  height: 30vh;
  position: relative;
  top: 0;
  left: 0;
  margin-top: 6rem;
  display: flex;
  /*   picture {
    width: 20%;
    height: 100%;

    img {
      height: 100%;
    }
  } */
}
@media (min-width: 768px) {
  .portrate {
    height: 65vh;
  }
}
@media (min-width: 1024px) {
  .portrate {
    height: 92vh;
  }
}
.portrate .portrate__slide {
  width: 500vw;
  display: flex;
  transition: all 1s ease;
}
.portrate .portrate__text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  background-color: rgba(0, 0, 0, 0.15);
  color: #FAFAFA;
  z-index: 9;
}
.portrate .portrate__arrows {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
}
@media (min-width: 768px) {
  .portrate .portrate__arrows {
    padding: 3rem;
  }
}

.portrate__arrows svg {
  cursor: pointer;
}

.portrate__text h2 {
  margin: 0;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .portrate__text h2 {
    font-size: 6rem;
  }
}
.portrate__text p {
  margin: 0;
  color: #FAFAFA;
}

.nav__language {
  width: 6.4rem;
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 1rem 0;
  cursor: pointer;
}

.language__select {
  width: 120%;
  position: absolute;
  top: 5.9rem;
  left: -0.5rem;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 0.7rem;
  background-color: #ECE7E3;
  display: none;
}

.nav__language:hover .language__select {
  display: flex;
}

.about {
  padding: 5rem 0 3rem;
  opacity: 0;
  transform: scale(0.8);
  transform: translateY(10rem);
  transition: all 0.7s ease;
}
@media (min-width: 768px) {
  .about {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    gap: 6rem;
  }
}
@media (min-width: 1024px) {
  .about {
    padding: 7rem 0 5rem;
    gap: 10rem;
  }
}
.about.visible {
  opacity: 1;
  transform: scale(1);
  transform: translateY(0);
}
@media (min-width: 768px) {
  .about picture {
    width: 45%;
    height: 50%;
  }
}
.about picture img {
  height: 100%;
}

.about__text {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .about__text {
    width: 55%;
  }
}
@media (min-width: 1024px) {
  .about__text {
    margin-top: 4rem;
  }
}
.about__text span {
  width: 100%;
  font-size: 5rem;
  font-weight: 300;
  font-family: "Loubag", sans-serif;
  text-transform: uppercase;
}
@media (min-width: 1024px) {
  .about__text span {
    font-size: 10rem;
  }
}
.about__text h2 {
  margin: 5rem 0 3rem;
  text-align: start;
  font-size: 3rem;
  font-weight: 700;
  color: #373737;
}
@media (min-width: 1024px) {
  .about__text h2 {
    font-size: 4rem;
  }
}
.about__text p {
  margin-bottom: 2rem;
  color: #373737;
  line-height: 4rem;
}

.links {
  padding: 5rem 0 4rem;
  opacity: 0;
  transform: scale(0.8);
  transform: translateY(10rem);
  transition: all 0.7s ease;
}
@media (min-width: 768px) {
  .links {
    padding: 0rem 0 10rem;
    display: flex;
    gap: 5rem;
    align-items: flex-end;
  }
}
.links.visible {
  opacity: 1;
  transform: scale(1);
  transform: translateY(0);
}

.links__text h3 {
  text-align: start;
}
@media (min-width: 1024px) {
  .links__text h3 {
    font-size: 4rem;
  }
}
.links__text p {
  margin-top: 1rem;
  line-height: 4rem;
}
@media (min-width: 1024px) {
  .links__text p {
    font-size: 1.7rem;
  }
}

.links__list {
  margin-top: 7rem;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5rem;
}
.links__list a {
  font-size: 5rem;
  font-family: "Sugar", sans-serif;
  font-weight: 700;
  color: #373737;
  transition: transform 0.1s ease-in-out, font-style 0.1s ease-in-out;
}
.links__list a:hover {
  font-style: italic;
  transform: translateX(2%);
}
@media (min-width: 768px) {
  .links__list a {
    font-size: 7rem;
  }
}
@media (min-width: 1024px) {
  .links__list a {
    font-size: 10rem;
  }
}
@media screen and (min-width: 1440px) {
  .links__list a {
    font-size: 13rem;
  }
}

footer {
  background-color: #373737;
  padding-top: 3rem;
}
footer h3 {
  color: #FAFAFA;
  font-size: 4rem;
}
footer .footer__links {
  width: 100%;
  display: flex;
  justify-content: center;
}
footer .footer__line {
  height: 1px;
  width: 80%;
  margin: 4rem auto 2rem;
  background-color: #FAFAFA;
}
footer .footer__end {
  text-align: center;
  padding-bottom: 2rem;
  color: #FAFAFA;
}

form {
  margin: 7rem 0 5rem;
}
form fieldset {
  border: 1px solid #71874E;
  margin-bottom: 4rem;
}
form legend {
  font-size: 3rem;
  font-family: "Loubag", sans-serif;
  text-transform: uppercase;
  color: #333333;
}
form label {
  margin-top: 3rem;
  font-weight: 700;
  text-transform: uppercase;
  display: block;
  font-family: "Loubag", sans-serif;
}
form input:not([type=submit]),
form textarea,
form select {
  width: 100%;
  padding: 1rem;
  display: block;
  background-color: #ECE7E3;
  border: 1px solid #71874E;
  border-radius: 1rem;
  margin-top: 0.5rem;
  margin-bottom: 2rem;
  font-family: "Loubag", sans-serif;
}
form input[type=file] {
  background-color: #71874E;
  color: #ECE7E3;
  padding: 1rem 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
form textarea {
  height: 20rem;
}

/* Paginas internas */
.portfolio__portrate {
  margin-top: 6rem;
  margin-bottom: 4rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .portfolio__portrate {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 10rem;
    margin-bottom: 0;
  }
}
@media (min-width: 1024px) {
  .portfolio__portrate {
    column-gap: 20rem;
  }
}
.portfolio__portrate h1 {
  text-align: start;
  font-size: 5rem;
}
@media (min-width: 768px) {
  .portfolio__portrate h1 {
    font-size: 7rem;
  }
}
@media (min-width: 1024px) {
  .portfolio__portrate h1 {
    font-size: 13rem;
  }
}
.portfolio__portrate p {
  margin-top: 2rem;
  line-height: normal;
}

/* Portfolio options */
.portfolio__options {
  padding: 2rem;
  opacity: 0;
  transform: scale(0.8);
  transform: translateY(10rem);
  transition: all 0.7s ease;
}
.portfolio__options.visible {
  opacity: 1;
  transform: scale(1);
  transform: translateY(0);
}
@media (min-width: 768px) {
  .portfolio__options {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 30rem;
    column-gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .portfolio__options {
    column-gap: 5rem;
    grid-template-rows: 40rem;
    margin-bottom: 10rem;
  }
}
.portfolio__options .options__box {
  margin-bottom: 6rem;
}
@media (min-width: 768px) {
  .portfolio__options .options__box {
    margin-bottom: 10rem;
  }
}
.portfolio__options .options__box picture, .portfolio__options .options__box img {
  height: auto;
}
@media (min-width: 768px) {
  .portfolio__options .options__box picture, .portfolio__options .options__box img {
    height: 100%;
  }
}
@media (min-width: 768px) {
  .portfolio__options .options__box h2 {
    font-size: 3.5rem;
  }
}
@media (min-width: 1024px) {
  .portfolio__options .options__box h2 {
    font-size: 5rem;
  }
}

/* Portfolio Proyects */
.portfolio__proyects {
  width: 100%;
  margin-top: 6rem;
  padding-top: 2rem;
}
.portfolio__proyects h1 {
  text-align: center;
  margin-bottom: 4rem;
  font-size: 3.5rem;
}
@media (min-width: 768px) {
  .portfolio__proyects h1 {
    font-size: 6rem;
  }
}
.portfolio__proyects h3 {
  text-align: center;
  margin-bottom: 4rem;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .portfolio__proyects h3 {
    font-size: 6rem;
  }
}
.portfolio__proyects .proyects__container {
  padding: 7rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 6rem;
}
@media (min-width: 768px) {
  .portfolio__proyects .proyects__container {
    gap: 14rem;
  }
}
@media (min-width: 1024px) {
  .portfolio__proyects .proyects__container {
    gap: 13rem;
  }
}

.proyects__box {
  margin-bottom: 5rem;
  /*   opacity: 0;
    transform: scale(0.8);
    transform: translateY(10rem);
    transition: all 0.7s ease; */
  /*   &.visible {
      opacity: 1;
      transform: scale(1);
      transform: translateY(0);
    } */
}
@media (min-width: 768px) {
  .proyects__box {
    display: flex;
    gap: 3rem;
  }
}
@media (min-width: 1024px) {
  .proyects__box {
    width: 80%;
    gap: 5rem;
  }
}
.proyects__box .proyects__text {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .proyects__box .proyects__text {
    margin-top: 3rem;
  }
}
.proyects__box h4 {
  text-align: start;
}
@media (min-width: 768px) {
  .proyects__box h4 {
    font-size: 6rem;
  }
}
@media (min-width: 1024px) {
  .proyects__box h4 {
    font-size: 8rem;
  }
}
.proyects__box span {
  text-transform: uppercase;
  font-family: "Loubag", sans-serif;
}
@media (min-width: 768px) {
  .proyects__box span {
    font-size: 2rem;
  }
}

@media (min-width: 768px) {
  .proyects__box--right {
    flex-direction: row-reverse;
    margin-left: auto;
  }
}
.proyects__box--right h4 {
  text-align: end;
}
.proyects__box--right .proyects__text {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.services {
  padding-top: 5rem;
  background-color: #ECE7E3;
}
.services .services__title {
  font-size: 6rem;
}
@media (min-width: 768px) {
  .services .services__title {
    margin-bottom: 5rem;
  }
}

.services__container {
  padding: 0 2rem 7rem;
}
@media (min-width: 1024px) {
  .services__container {
    display: grid;
    grid-template-columns: 1fr 0.5rem 1fr;
    grid-template-rows: repeat(4, 54rem);
    column-gap: 3rem;
    row-gap: 5rem;
